@keyframes slideInFromLeft {
    0% {
        transform: translateX(-100%);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slideInFromRight {
    0% {
        transform: translateY(-100%);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slideInFromTop {
    0% {
        transform: translateY(-100%);
        opacity: 0;
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

.infinite-animation-left {
    animation: slideInFromLeft 3s ease-in-out infinite;
}

.infinite-animation-right {
    animation: slideInFromRight 3s ease-in-out infinite;
}

.infinite-animation-top {
    animation: slideInFromTop 3s ease-in-out infinite;
}


@keyframes scaleButton {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.2);
    }

    100% {
        transform: scale(1);
    }
}

.infinite-animation-button {
    animation: scaleButton 2s ease-in-out infinite;
}


